/* Estilos por defecto para el elemento .uno */
.uno {
    background-color: var(--azul);
  }

.prinpal{
  background-color: #f7f7f7;
}
  
  /* Media query para dispositivos con un ancho máximo de 767px (dispositivos móviles) */
  @media (max-width: 767px) {
    .uno {
      overflow-y: auto; /* Agrega una barra de desplazamiento vertical automáticamente */
      max-height: 100vh; /* Establece la altura máxima al 80% del alto de la pantalla */
    }
  }
  